
@import url(../../CSS/normalize.css);
html {
  width: 100%;
  height: 100%;
  background: #f0f0f0;
}
* {
  font-family: "Microsoft YaHei", Arial, sans-serif;
  padding: 0;
  margin: 0;
}
ul {
  list-style: none;
  padding: 0;
}
li {
  list-style: none;
}
input {
  border: 0;
  outline: none;
}
input:focus {
    outline: none;
}
button {
  border: 0;
}
a {
  text-decoration: none;
}
svg {
  vertical-align: middle;
}
.clearfix:before, .clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
  overflow: hidden;
}
.clearfix {
  zoom: 1;
}
body {
  background: #f0f0f0;
}
#app {
  background: #f0f0f0;
}
.lv-container {
  width: 1214px;
  min-width: 1213px;
  margin: 0 auto;
  overflow: hidden;
}
.side-nav {
  position: fixed;
  left: 0;
  top: 90px;
}
.main {
  width: 1200px;
  margin: 0 auto;
  *zoom: 1;
}
.main:before, .main:after {
  content: "";
  display: table;
}
.main:after {
  clear: both;
}



