
footer {
  clear: both;
  min-width: 1349px;
  height: 50px;
  margin-top: 50px;
  background: #339966;
}
footer ul {
    width: 1200px;
    margin: 0 auto;
    text-align: center;
}
footer ul li {
      display: inline-block;
      margin-top: 1.5em;
      padding-left: 1em;
      padding-right: 1em;
      border-right: 1px solid #fff;
      vertical-align: bottom;
      color: #fff;
}
li:last-child {
  border-right: 0;
}



